/* Canvas and game area styles */
canvas {
  width: 100%;
  height: 100%;
  background: transparent;
  display: block;
  image-rendering: pixelated;
}

.scan-lines {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 2px,
    rgba(0, 255, 255, 0.03) 2px,
    rgba(0, 255, 255, 0.03) 4px
  );
  z-index: 1000;
}