/* Theater Curtain Animation */
.curtain-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  pointer-events: none;
}

.curtain {
  position: absolute;
  top: 0;
  width: 50%;
  height: 100%;
  background: 
    linear-gradient(45deg, 
      #8b0000 0%, 
      #dc143c 25%, 
      #8b0000 50%, 
      #dc143c 75%, 
      #8b0000 100%
    );
  background-size: 40px 40px;
  box-shadow: 
    inset 0 0 50px rgba(0, 0, 0, 0.5),
    inset 0 0 100px rgba(139, 0, 0, 0.4);
  transition: transform 2s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  opacity: 1;
}

.curtain::before {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  background: 
    repeating-linear-gradient(
      90deg,
      transparent 0px,
      rgba(255, 255, 255, 0.2) 2px,
      transparent 4px,
      rgba(0, 0, 0, 0.2) 6px,
      transparent 8px
    );
  opacity: 1;
}

.curtain::after {
  content: '';
  position: absolute;
  top: 0;
  width: 100%;
  height: 30px;
  background: 
    linear-gradient(to bottom,
      rgba(255, 215, 0, 1) 0%,
      rgba(255, 215, 0, 0.8) 50%,
      rgba(255, 215, 0, 0.2) 100%
    );
  box-shadow: 
    0 5px 15px rgba(255, 215, 0, 0.5),
    0 10px 25px rgba(255, 215, 0, 0.3);
}

.curtain-left {
  left: 0;
  transform-origin: left center;
  background-image: 
    linear-gradient(to right, 
      rgba(0, 0, 0, 0.2) 0%, 
      transparent 20%
    );
}

.curtain-right {
  right: 0;
  transform-origin: right center;
  background-image: 
    linear-gradient(to left, 
      rgba(0, 0, 0, 0.2) 0%, 
      transparent 20%
    );
}

/* Curtain opening animation */
.curtain-container.opening .curtain-left {
  transform: translateX(-100%);
}

.curtain-container.opening .curtain-right {
  transform: translateX(100%);
}

/* Western Background */
.western-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: 
    /* Mountains */
    radial-gradient(ellipse at 20% 100%, 
      transparent 0%, 
      transparent 40%, 
      #8b7355 40%, 
      #8b7355 60%, 
      transparent 60%
    ),
    radial-gradient(ellipse at 60% 100%, 
      transparent 0%, 
      transparent 45%, 
      #a0522d 45%, 
      #a0522d 65%, 
      transparent 65%
    ),
    radial-gradient(ellipse at 85% 100%, 
      transparent 0%, 
      transparent 35%, 
      #8b7355 35%, 
      #8b7355 55%, 
      transparent 55%
    ),
    /* Sky gradient */
    linear-gradient(to bottom,
      #87ceeb 0%,
      #add8e6 30%,
      #f0e68c 70%,
      #daa520 85%,
      #cd853f 100%
    );
  overflow: hidden;
  animation: desertWind 20s ease-in-out infinite;
}

/* Ground and street */
.western-background::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background: 
    /* Street perspective */
    linear-gradient(to bottom,
      #d2691e 0%,
      #cd853f 20%,
      #8b4513 60%,
      #654321 100%
    );
  clip-path: polygon(
    0% 100%,
    0% 0%,
    40% 0%,
    45% 100%,
    55% 100%,
    60% 0%,
    100% 0%,
    100% 100%
  );
}

@keyframes desertWind {
  0%, 100% { 
    filter: hue-rotate(0deg) brightness(1); 
  }
  25% { 
    filter: hue-rotate(5deg) brightness(1.05); 
  }
  50% { 
    filter: hue-rotate(0deg) brightness(0.95); 
  }
  75% { 
    filter: hue-rotate(-5deg) brightness(1.02); 
  }
}